$BackColor: #2B2B2B;
$HeaderColor: #191919;
$Font: 'Roboto', sans-serif;
$FontColor: #EFEFEF;
$desktop-max: 1920px;
$desktop-min: 1025px;
$tablet-max: 1024px;
$tablet-min: 768px;
$phone-max: 740px;
$phone-min:320px;

body {
    margin: 0 auto;
    padding: 0;
    width: 100%;
    height: 100%;
    background-color: $BackColor ;
}

.container {
    margin: 0 auto;
    padding: 0;
    width: 95%;
    max-width: 1920px;
    min-width: 1024px;
    text-align: center;

}
   

.table {
    margin: 0 auto;
    padding: 0;
    width: 100%;
    
      
}


.col-1 {
            width: 8.33333333333%;

        }

        .col-2 {
            width: 16.6666666667%;
        }
        .col-3 {
            width:25%;
        }
        .col-4 {
            width: 33.3333333333%;

        }
        .col-5 {
            width: 41.6666666667%;
        }
        .col-6 {
            width:50%;
        }
        .col-7 {
            width: 58.3333333333%;
        }
        .col-8 {
            width:66.6666666666%;
        }
        .col-9 {
            width:75%;
        }
        .col-10 {
            width: 83.3333333333%;
        }
        .col-11 {
             width:91.6666666666%; 
        }
        .col-12 {
             width: 100%;
        }

        [class*="col-"] {
          float:left; 
        }

        [class*="col-"]:last-of-type {
        }

        .row:before
            :after
        {
          clear: both;
          content:"";
        } 

.menu{
	display: none;
	width:50px;
	height: 40px;
    padding:1em; 
    
    
}
	
.menu:hover{
	cursor:pointer
}
.menu li{
	list-style:none;
	margin:5px 0;
	width:50px;
	height:8px;
	background:#949494;
}
.on{
	transform:rotate(90deg);
}
.on li{
	background:#ddd
}

nav{
    background-color: $BackColor;
    text-align:right;
    
    
}
	
nav ul li{
	display:inline-block;
    line-height: 2;
    text-align: center;
}


  
nav a{
	display:block;
	font-family: $Font;
    font-size: 100%;
    text-decoration: none;
    color: $FontColor;
    margin-left: 15%;
}

nav a:hover{
	color:#999
}
a, .menu, .menu li{
	
	transition: all 0.3s ease-out
}

	
#logo-holder {
    
    margin: 0 auto;
    padding: 0;
    position: relative;
    top:  25%;
    left:0%;
    
    .logo {
        margin: 0;
        padding: 0;
        width: 100%;
    }
}

#links{
    margin: 0 ;
    padding: 0;
    margin-top: 15%;
   position: relative;
    top:  25%;
    right: 15%;
    
    .media-links{
        margin: 0 auto;
        width: 25%;
       display: inline;
        margin-top: 30%;    
        margin-left: 5%;
       
    }
    
    #inner {
        margin: 0 auto;
        padding: 0;
        text-align: center;
        position: relative;
        top: 10%;
      
    }
    
    #insta {
        margin-left: 5%;
    }
    #phone {
        margin-left: 5%;
    }
 }

    h1{
        margin: 0 auto;
        font-family: $Font;
        text-decoration: none;
        color: $FontColor;
        font-size: 150%;
        margin-left: 6%;
        margin-top: 2%;
    }

/* Tablet Portait */

@media only screen 
  and (min-device-width: 768px) 
  and (max-device-width: 1024px) 
  and (orientation: portrait) 
  and (-webkit-min-device-pixel-ratio: 1) {

      .container {
        margin: 0 auto;
        padding: 0;
        width: 100%;
        max-width: $tablet-max;
        min-width: $tablet-min;
          
         .menu{
			display:block;
    	
		}
        nav{
			width: 85%;
			display:none;
			background:$BackColor;
            text-align: center;
            padding: 0;
            padding-bottom: 2%;
		}
        nav ul li{
			display:block;
			border-bottom:1px solid #ccc;
            
		}
        nav ul li:last-child{
            border:none
        }
        
        nav a{
            font-size: 140%;
        }
        
        #logo-holder {
            width: 80%;
            left: 10%;
            
            .logo {
                width: 90%;
            }
        }
        
        #links {
            width: 80%;
            right: 0;
            left: 10%;
        }
        #left, #right{
            width: 50%;
        
            .media-links {
                width: 40%;
                
                    
            }
            
            h1 {
                display: none;
            }
        }
    }   
}

/* Tablet Landscape */

@media only screen 
  and (min-device-width: 768px) 
  and (max-device-width: 1024px) 
  and (orientation: landscape) 
  and (-webkit-min-device-pixel-ratio: 1) {

    .container {
        margin: 0 auto;
        padding: 0;
        width: 100%;
        max-width: $tablet-max;
        min-width: $tablet-min;
      
          .menu{
			display:block;
    	
		}
        nav{
			width: 85%;
			display:none;
			background:$BackColor;
            text-align: center;
            padding: 0;
            padding-bottom: 2%;
		}
        nav ul li{
			display:block;
			border-bottom:1px solid #ccc;
            
		}
        nav ul li:last-child{
            border:none
        }
        
        nav a{
            font-size: 140%;
        }
        
        #logo-holder {
            width: 33.3333333333%;
            left: 33%;
            
            .logo {
                width: 100%;
            }
        }
        
        #links {
            width: 90%;
            left: 0;
            
            .media-links {
                width: 35%;
                margin-top: 5%;
                
                #email {
                    margin-right: 20%;
                }
            }
            h1 {
                font-size: 90%;
            }
        }
      }
}



/* Mobile Portrait */


@media only screen 
  and (min-device-width: 320px) 
  and (max-device-width: 740px) 
  and (-webkit-min-device-pixel-ratio: 2)
  and (orientation: portrait) { 
    
    .container {
        margin: 0 auto;
        padding: 0;
        width: 100%;
        max-width: 480px;
        min-width: $phone-min;
        
        .menu{
			display:block;
    	
		}
        nav{
			width: 85%;
			display:none;
			background:$BackColor;
            text-align: center;
            padding: 0;
            padding-bottom: 2%;
		}
        nav ul li{
			display:block;
			border-bottom:1px solid #ccc;
            text-align: center;
            
		}
        nav ul li:last-child{
            border:none
        }
        
        nav a{
            font-size: 100%;
        }
        
        #logo-holder {
            width: 80%;
            left: 10%;
            
            .logo {
                width: 90%;
            }
        }
        
        #links {
            width: 80%;
            right: 0;
            left: 10%;
            background-color: red;
            text-align: center;
        }
        #left, #right{
            width: 50%;
            text-align: center;
        
            .media-links {
                width: 40%;
            }
            
            
            h1 {
                display: none;
            }
        }
    }   
}

/* Mobile Landscape */

@media only screen 
  and (min-device-width: 320px) 
  and (max-device-width: 740px) 
  and (-webkit-min-device-pixel-ratio: 2)
  and (orientation: landscape) { 
    
    .container {
        margin: 0 auto;
        padding: 0;
        width: 100%;
        max-width: $phone-max;
        min-width: $phone-min;
        
        .menu{
			display:block;
    	
		}
        nav{
			width: 85%;
			display:none;
			background:$BackColor;
            text-align: center;
            padding: 0;
            padding-bottom: 2%;
		}
        nav ul li{
			display:block;
			border-bottom:1px solid #ccc;
            text-align: center;
            
		}
        nav ul li:last-child{
            border:none
        }
        
        nav a{
            font-size: 100%;
        }
        
        #logo-holder {
            width: 50%;
            left: 25%;
            
            .logo {
                width: 100%;
            }
        }
         #links {
            width: 90%;
            left: 0;
            margin-top: 8%;
             
             #left, #right {
                 text-align: center;
             }
            
            .media-links {
                width:35%;
                margin-top: 5%;
            
            }
            h1 {
                display: none;    
                
            }
        }
    }   
}







